// webSocket
import { Toast } from 'vant'
// 
var websock = null
var globalCallback = null
var timer = null
var timer2 = null
// 
function initWebSocket (agentData) {
  if ('WebSocket' in window) {
    var url = `${location.protocol === 'https' ? 'wss' : 'ws'}://${location.host}/ws/live/detail?token=aaaaa`
    console.log(url)
    websock = new WebSocket(url)
    websock.onmessage = (e) => {
      websocketonmessage(e)
    }
    websock.onclose = (e) => {
      websocketclose(e)
    }
    websock.onopen = () => {
      websocketOpen()
      if (agentData) {
        websocketsend(agentData)
      }
    }
    // 
    websock.onerror = (event) => {
      console.log('WebSocket')
      Toast.fail('WebSocket')
    }
  } else {
    Toast.fail('WebSocket,!')
    return false
  }
}

// 
function websocketonmessage (e) {
  console.log('', e)
  globalCallback(e)
  // globalCallback(JSON.parse(e.data))
}

// 
function websocketsend (agentData) {
  if (agentData) {
    console.log('', agentData)
    if (typeof agentData === 'string') {
      websock.send(agentData)
    } else {
      websock.send(JSON.stringify(agentData))
    }
  }
}

// 
export function websocketclose (e) {
  console.log(``)
  clearTimeout(timer2)
  timer2 = null
  timer2 = setTimeout(() => {
    initWebSocket()
  }, 1000)
}
export function handleWebsocketClose (e) {
  if (websock) {
    console.log(``)
    websock.close()
  }
}
//  websocket 
function websocketOpen (e) {
  console.log('')
}

/**
 * 
 *
 * @export
 * @param {*} agentData
 * @param {*} callback
 * @returns sendSock(data, this.getConfigResult)
 */
export function sendSock (agentData, callback) {
  if (agentData) {
    globalCallback = callback
    if (websock && websock.readyState === websock.OPEN) {
      // ws
      websocketsend(agentData)
    } else if (websock && websock.readyState === websock.CONNECTING) {
      //  1s
      clearTimeout(timer)
      timer = null
      timer = setTimeout(() => {
        sendSock(agentData, callback)
      }, 1000)
    } else {
      // 
      initWebSocket(agentData)
    }
  } else {
    Toast('')
  }
}
